const CAPPED_WIDTH = window.innerWidth * (window.devicePixelRatio || 1);
const CAPPED_HEIGHT = window.innerHeight * (window.devicePixelRatio || 1);
const isMobileRatio = window.innerHeight > window.innerWidth;
// we want to cap to the width when it is the largest dimension (for desktop purposes)
// in case of mobiles, to take care of fullscreen images, we should take the height into account
const responsiveCappingSuffix = isMobileRatio ? `?h=${CAPPED_HEIGHT}` : `?w=${CAPPED_WIDTH}`;

export const cappedImageUrl = (bareUrl, cappedWidth?) => {
  const capSuffix = cappedWidth ? `?w=${cappedWidth}` : responsiveCappingSuffix;
  return bareUrl + capSuffix;
};

// deprecate. not needed
export const lowQualityCappedImageUrl = (bareUrl, cappedWidth?) => {
  const cW = cappedWidth ? cappedWidth : CAPPED_WIDTH;
  return bareUrl + `?w=${cW}&q=4`;
};

export const thumbImageUrl = (bareUrl) => {
  return bareUrl + `?w=250`;
};
