export const isSSR = () => {
  try {
    // webpack replaces "global" with "this", thus in browser env it will be "window", and in NodeJS env it will be "global"
    //	@ts-ignore
    return window !== global;
  } catch (e) {
    // in NodeJS window is undefined, and above check throws an error
    return true;
  }
};

if (isSSR()) {
  require('./ssr');
} else {
  require('intersection-observer');
  require('whatwg-fetch');
}
