import React, { ComponentType } from 'react';
import cx from 'classnames';

import { connectedElement } from '../tipser-context';
import { Contextable } from '../tipser-context';
import { EVENT_REMOVE_FROM_CART, EVENT_ADD_TO_CART } from './cart-events';
import { AddedToCartPopup } from '../components/added-to-cart-popup';
import { withSentry } from '../common/sentry/with-sentry';
import { withInViewTracker, Tracking, TrackableProps } from '../common/analytics';
import CartIcon from './cart.svg';

import './cart-icon.less';
import ButtonClear from '../common/button-clear/button-clear';

export interface CartProps extends Contextable, TrackableProps {
  cartSize?: number;
  onClick?: () => void;
}

export interface CartState {
  cartSize?: number;
  animationInProgress?: boolean;
}

class CartNotConnected extends React.Component<CartProps, CartState> {
  constructor(props: CartProps) {
    super(props);
    this.state = {
      cartSize: props.cartSize || 0,
      animationInProgress: false,
    };
  }

  componentDidMount() {
    this.updateCartSize();
    this.props.context.events.register(EVENT_ADD_TO_CART, this.updateCartSize);
    this.props.context.events.register(EVENT_REMOVE_FROM_CART, this.updateCartSize);
    this.props.context.tipserSdk.addDialogClosedListener(this.updateCartSize);
    this.props.context.tipserSdk.addCartItemsQuantityChangeListener(this.updateSizeInState);
  }

  componentWillUnmount() {
    this.props.context.tipserSdk.removeDialogClosedListener(this.updateCartSize);
    this.props.context.tipserSdk.removeCartItemsQuantityChangeListener(this.updateSizeInState);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.cartSize !== null && prevProps.cartSize !== this.props.cartSize) {
      this.updateCartSize();
    }
  }

  updateSizeInState = (cartSize) => {
    if (this.state.cartSize !== cartSize) {
      this.setState(
        {
          cartSize,
          animationInProgress: true,
        },
        () => {
          setTimeout(
            () =>
              this.setState({
                animationInProgress: false,
              }),
            2000
          );
        }
      );
    }
  };

  updateCartSize = () => {
    setTimeout(() => {
      this.props.context.tipserSdk.getCurrentCartSize().then(this.updateSizeInState);
    }, 500); // This timeout is needed to avoid timing issues between refreshing SDK cart size cache and
    //calling getCurrentCartSize. This needs to be further investigated and preferably the timeout should be removed.
  };

  onClick = () => {
    if (this.props.onClick) {
      this.props.onClick();
    } else {
      this.props.context.tipserSdk.openPurchaseDialog();
    }
    Tracking.dispatchCartTabClick();
  };

  renderCartSizeIndicator = (cartSize: number) => {
    return (
      <span
        className={cx('tipser-cart-size-indicator te-primary-fill', {
          'tipser-cart-size-indicator_infinity': cartSize > 99,
          'tipser-cart-size-indicator_animated': this.state.animationInProgress,
        })}
      >
        {cartSize}
      </span>
    );
  };

  render() {
    const { cartSize } = this.state;
    const {
      context: { defaultAddedToCartPopup },
    } = this.props;
    return (
      <React.Fragment>
        <div className="cart-icon">
          <ButtonClear
            /* "tipser-icon-cart" is used by POS (https://plansverige.org/shop/godnattsagor-for-rebelltjejer/) to trigger click via JS */
            className="tipser-icon-cart"
            onClick={this.onClick}
          >
            <CartIcon />
            {cartSize > 0 && this.renderCartSizeIndicator(cartSize)}
          </ButtonClear>
        </div>
        {defaultAddedToCartPopup && <AddedToCartPopup />}
      </React.Fragment>
    );
  }
}

export const Cart: ComponentType<CartProps> = withSentry(connectedElement(CartNotConnected));
export const TrackableCart: ComponentType<CartProps> = withInViewTracker({
  trackingCallback: () => Tracking.dispatchCartTabView(),
})(Cart);
