import React from 'react';

import { Contextable } from './tipser-context';

import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const UNKNOWN_ERROR_OCCURED = 'Error occured';

let eventsDispatcher: (event: string, params?: EventError) => void = null;
export const EVENT_ERROR = 'onError';

export interface EventError {
  type?: string;
  id?: string;
  message?: string;
  stack?: string;
}

export class TipserElementError implements EventError {
  public type = 'TipserElementError';

  public constructor(public id?: string, public message?: string) {
    if (Error.captureStackTrace) {
      // because of IE11...
      Error.captureStackTrace(this, TipserElementError);
    }
  }
}

export const handleError = (error?: any) => {
  if (error && error.message) {
    eventsDispatcher(EVENT_ERROR, error);
  } else if (error && error.error && error.error.id && error.error.message) {
    eventsDispatcher(EVENT_ERROR, new TipserElementError(error.error.id, error.error.message));
  } else {
    console.log(error);
    throw new Error('Uknown error');
  }
};

export class ErrorPopup extends React.Component<Contextable> {
  componentDidMount() {
    const { context } = this.props;
    eventsDispatcher = context.events.dispatch;
    if (context.useDefaultErrorHandler) {
      context.events.register(EVENT_ERROR, this.onError);
    }
  }

  onError(params) {
    toast.error(params && params.message ? params.message : UNKNOWN_ERROR_OCCURED);
  }

  render() {
    return <ToastContainer />;
  }
}
