import React from 'react';
import ProductBasicInfo from '../product/product-basic-info';
import ButtonCross from '../../common/button-cross/button-cross';
import { t } from '../../i18n';

import { TipserProductModel } from '../product/model';
import { PrimaryButton } from '../../common/button-primary/button-primary';

import './added-to-cart-popup.less';
import { customBemNamesFactory } from '../../common/utils';

const bem = customBemNamesFactory('te-added-to-cart');
const bemClasses = {
  main: bem(),
  header: bem('header'),
  title: bem('title'),
  imgContainer: bem('img-container'),
  productBasicInfo: bem('product-basic-info'),
};

interface AddedToCartDumbPropTypes {
  onCheckoutClick: () => void;
  onClose: () => void;
  product: TipserProductModel;
}

const AddedToCartPopupDumb: React.FunctionComponent<AddedToCartDumbPropTypes> = ({
  onCheckoutClick,
  onClose,
  product,
}) => {
  const hasImage = product.images.length !== 0;
  return (
    <div className={bemClasses.main}>
      <div className={bemClasses.header}>
        <h3 className={bemClasses.title}>{t('added_to_cart')}</h3>
        <ButtonCross onClick={onClose} />
      </div>

      <div className={bemClasses.imgContainer}>{hasImage && <img src={product.images[0]['250x']} />}</div>

      <ProductBasicInfo className={bemClasses.productBasicInfo} product={product} />

      <PrimaryButton onClick={onCheckoutClick}>{t('checkout_button_label')}</PrimaryButton>
    </div>
  );
};

export default AddedToCartPopupDumb;
