import { Subscriber, SubscriberType } from '.';

export class HtmlDocumentSubscriber implements Subscriber {
  type: SubscriberType = 'external';

  sendEvent<T>(detail: T) {
    try {
      const event = new CustomEvent<T>('tw-track', { detail });
      window.document.dispatchEvent(event);
    } catch (e) {
      // Sorry IE
    }
  }
}
