import React, { ComponentType } from 'react';
import get from 'lodash.get';
import ReactResizeDetector from 'react-resize-detector';

import { connectedElement, Contextable } from '../../tipser-context';
import { fetchTipserProduct } from '../../api/tipser-api';
import { ProductRenderer, PRODUCT_ALTERNATIVE_RENDERER } from './product-renderer';
import { ProductCardRenderer, ImageSize } from './product-card-renderer';
import { t } from '../../i18n';
import { TipserProductModel, TipserProductVariantModel } from './model';
import { EVENT_ADD_TO_CART } from '../../shopping/cart-events';
import { handleError } from '../../tipser-error';
import { TrackableProps } from '../../common/analytics';
import { LoaderCenter } from '../../common/loader';
import { getProductFromServerState } from '../../state-builder';

export enum DataTipserViewType {
  COMPACT = 'compact',
  FULL = 'full',
}

interface TipserProductState {
  product?: TipserProductModel;
  selectedVariantOption?: { label: string; value: string };
  variantSelectorValidationError: boolean;
  addedToCart?: boolean;
  isRejected: boolean;
  isPending: boolean;
  hasErrorAddingToCart: boolean;
}

export interface TipserProductProps extends Contextable, TrackableProps {
  posId?: string;
  dontLoadProduct?: boolean;
  productId?: string;
  product?: TipserProductModel;
  renderer?: PRODUCT_ALTERNATIVE_RENDERER;
  imgSize?: ImageSize;
  viewMode?: DataTipserViewType;
  onProductClick?: (product: TipserProductModel) => void;
  disableCarouselWhenOpenModal?: (bool) => any;
  descriptionExpanded?: boolean;
}

class TipserProductComponentNotConnected extends React.Component<TipserProductProps, TipserProductState> {
  constructor(props: TipserProductProps) {
    super(props);
    this.state = {
      product: props.product || getProductFromServerState(props.context, props.productId) || undefined,
      selectedVariantOption: undefined,
      variantSelectorValidationError: false,
      addedToCart: false,
      isRejected: false,
      isPending: false,
      hasErrorAddingToCart: false,
    };
  }

  private setTrackingData(product: TipserProductModel): void {
    if (this.props.setTrackingData) {
      this.props.setTrackingData(product);
    }
  }

  fetchProduct = () => {
    const productFromServerState = getProductFromServerState(this.props.context, this.props.productId);
    if (productFromServerState) {
      this.setTrackingData(productFromServerState);
    } else {
      fetchTipserProduct(this.props.productId, this.getPosId())
        .then((product) => {
          this.setTrackingData(product);

          this.setState({
            product,
            isPending: false,
          });
        })
        .catch(() => {
          this.setState({
            isRejected: true,
          });
        });
    }
  };

  componentDidMount() {
    !this.props.dontLoadProduct && this.fetchProduct();
  }

  componentDidUpdate(prevProps) {
    if (this.props.productId !== prevProps.productId) {
      this.setState(
        {
          isPending: true,
          product: null,
          hasErrorAddingToCart: false,
        },
        () => {
          !this.props.dontLoadProduct && this.fetchProduct();
        }
      );
    }
  }

  isAddToCartAllowed() {
    return !this.state.product.variants || this.state.product.variants.length < 2 || this.state.selectedVariantOption;
  }

  addToCartClicked = () => {
    if (this.isAddToCartAllowed()) {
      const productId = this.state.product.id;
      const selectedVariant = this.getSelectedVariant();
      const variantId = get(selectedVariant, 'id', productId);
      const fullProductId = `${productId}_${variantId}`;
      const tipserSdk = this.props.context.tipserSdk;

      const { directToChekoutMode } = this.props.context.addToCartSettings;

      const addToCartActionToBind = directToChekoutMode ? tipserSdk.openDirectToCheckoutDialog : tipserSdk.addToCart;
      const addToCartAction = addToCartActionToBind.bind(tipserSdk);

      return addToCartAction(fullProductId)
        .then(tipserSdk.getCurrentCartSize.bind(tipserSdk))
        .then((cartSize) => {
          const product = this.state.isPending ? this.props.product : this.state.product;
          this.props.context.events.dispatch(EVENT_ADD_TO_CART, {
            cartSize,
            product,
          });
        })
        .catch((error) =>
          this.setState(
            {
              hasErrorAddingToCart: true,
            },
            () => handleError(error)
          )
        );
    } else {
      this.setState({
        variantSelectorValidationError: true,
      });
    }
  };

  handleVariantChange = (selectedVariantOption: { value: string; label: string }) => {
    this.setState({
      variantSelectorValidationError: false,
      selectedVariantOption,
    });
  };

  getPosId() {
    return this.props.posId ? this.props.posId : this.props.context.posId;
  }

  getSelectedVariant(): TipserProductVariantModel | undefined {
    const { product, selectedVariantOption } = this.state;
    if (selectedVariantOption) {
      return product.variants.find((variant) => variant.id === selectedVariantOption.value);
    }
    if (product && product.variants && product.variants.length > 0) {
      return product.variants[0];
    }
    return undefined;
  }

  render() {
    const renderer = this.props.renderer || false;
    const { selectedVariantOption, variantSelectorValidationError, isPending } = this.state;
    const product = isPending ? this.props.product : this.state.product;
    const imgSize = this.props.imgSize || '1.0';

    return (
      <React.Fragment>
        {product && !renderer && (
          <ReactResizeDetector handleWidth>
            <ProductRenderer
              {...{
                product: product,
                posId: this.getPosId(),
                handleVariantChange: this.handleVariantChange,
                addToCartClicked: this.addToCartClicked,
                addedToCart: this.state.addedToCart,
                selectedVariantOption: selectedVariantOption,
                selectedVariant: this.getSelectedVariant(),
                variantSelectorValidationError: variantSelectorValidationError,
                hasErrorAddingToCart: this.state.hasErrorAddingToCart,
                descriptionExpanded: true,
              }}
            />
          </ReactResizeDetector>
        )}

        {product && renderer === PRODUCT_ALTERNATIVE_RENDERER.CARD && (
          <ReactResizeDetector handleWidth>
            <ProductCardRenderer
              {...{
                posId: this.getPosId(),
                product: product,
                imgSize: imgSize,
                viewMode: this.props.viewMode,
                selectedVariant: this.getSelectedVariant(),
                onProductClick: this.props.onProductClick,
                disableCarouselWhenOpenModal: this.props.disableCarouselWhenOpenModal,
              }}
            />
          </ReactResizeDetector>
        )}

        {this.state.isPending && !product && <LoaderCenter delayed />}

        {this.state.isRejected && <div className="te-product-placeholder">{t('product_not_available_temp')}</div>}
      </React.Fragment>
    );
  }
}

export const TipserProductComponent: ComponentType<TipserProductProps> = connectedElement(
  TipserProductComponentNotConnected
);
