import { TrackingPayload } from './tracking-payload';
import { Subscriber, SubscriberType } from './subscribers';

export class Analytics {
  private subscribers: Array<Subscriber> = [];

  private sendEvent(event: TrackingPayload, subscriberType: SubscriberType = 'all') {
    this.subscribers
      .filter((_) => _.type === subscriberType || subscriberType === 'all')
      .forEach((subscriber) => subscriber.sendEvent(event));
  }

  registerSubscriber(subscriber: Subscriber) {
    this.subscribers.push(subscriber);
  }

  dispatchEvent(payload: TrackingPayload) {
    this.sendEvent(payload);
  }

  dispatchInternalEvent(payload: TrackingPayload) {
    this.sendEvent(payload, 'internal');
  }

  dispatchExternalEvent(payload: TrackingPayload) {
    this.sendEvent(payload, 'external');
  }
}
