import React, { ComponentType } from 'react';
import ReactResizeDetector from 'react-resize-detector';
import Slider, { Settings } from 'react-slick';
import cx from 'classnames';
import marked from 'marked';
import InnerImageZoom from 'react-inner-image-zoom';

import { t } from '../../i18n';
import { TipserProductModel, TipserProductVariantModel } from './model';
import { Dropdown } from '../../common/dropdown/dropdown';
import { connectedElement, Contextable } from '../../tipser-context';
import ProductBasicInfo from './product-basic-info';
import { PrimaryButton } from '../../common/button-primary/button-primary';
import { CloudinaryImg } from '../../common/cloudinary-img';
import { SmallArrowDown } from '../../common/icons';
import { CarouselArrow } from '../collection/carousel-arrow';

import { BREAKPOINTS } from '../../../src/common/utils';

import './product.less';

export enum PRODUCT_ALTERNATIVE_RENDERER {
  CARD = 'card',
}

interface ProductRendererProps extends Contextable {
  product?: TipserProductModel;
  handleVariantChange?: any;
  addToCartClicked?: () => Promise<void>;
  selectedVariantOption?: any;
  selectedVariant?: TipserProductVariantModel;
  variantSelectorValidationError?: any;
  addedToCart?: any;
  hasErrorAddingToCart?: boolean;
  descriptionExpanded?: boolean;
}

interface ProductRendererState {
  descriptionVisible: boolean;
  descriptionMore: boolean;
  currentImageIndex: number;
  addedToCart: boolean;
}

class ProductRendererNotConnected extends React.Component<ProductRendererProps, ProductRendererState> {
  sliderSettings: Settings = {
    vertical: true,
    verticalSwiping: true,
    infinite: false,
  };

  constructor(props) {
    super(props);
    this.state = {
      descriptionVisible: this.props.descriptionExpanded,
      descriptionMore: false,
      currentImageIndex: 0,
      addedToCart: props.addedToCart,
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.addedToCart !== this.props.addedToCart) {
      this.setState({
        addedToCart: this.props.addedToCart,
      });
    }
  }

  toogleDescriptionVisible() {
    if (!this.props.descriptionExpanded) {
      this.setState({
        descriptionVisible: !this.state.descriptionVisible,
      });
    }
  }
  toggleDescriptionMore = () => {
    this.setState({
      descriptionMore: !this.state.descriptionMore,
    });
  };

  changeCurrentImageIndex(index) {
    this.setState({
      currentImageIndex: index,
    });
  }

  addToCartClicked() {
    this.props.addToCartClicked &&
      this.props.addToCartClicked().then(() => {
        this.setState({
          addedToCart: true,
        });
        setTimeout(() => {
          this.setState({
            addedToCart: false,
          });
        }, 2000);
      });
  }

  handleVariantChange = (event) => {
    this.props.handleVariantChange(event);
    this.changeCurrentImageIndex(0);
  };

  getClassNameByWidth = (width: number): string => {
    if (width <= BREAKPOINTS.XSMALL) {
      return 'te-single-product';
    } else if (width <= BREAKPOINTS.SMALL) {
      return 'te-single-product__column';
    } else {
      return 'te-product-full';
    }
  };

  handleContainerWidthChange = (width) => {
    if (width <= BREAKPOINTS.XSMALL && this.sliderSettings.slidesToScroll != 3) {
      this.sliderSettings = {
        ...this.sliderSettings,
        slidesToScroll: 3,
        slidesToShow: 3,
        vertical: false,
        verticalSwiping: false,
      };
    } else if (width <= BREAKPOINTS.SMALL && width > BREAKPOINTS.XSMALL && this.sliderSettings.slidesToScroll != 4) {
      this.sliderSettings = {
        ...this.sliderSettings,
        slidesToShow: 4,
        slidesToScroll: 4,
        vertical: true,
        verticalSwiping: true,
      };
    } else if (width > BREAKPOINTS.SMALL && this.sliderSettings.slidesToScroll != 5) {
      this.sliderSettings = {
        ...this.sliderSettings,
        slidesToShow: 5,
        slidesToScroll: 5,
        vertical: true,
        verticalSwiping: true,
      };
    }
  };

  render() {
    const { product, selectedVariant, selectedVariantOption, variantSelectorValidationError } = this.props;
    const images = selectedVariantOption && selectedVariant.images.length > 0 ? selectedVariant.images : product.images;
    const { currentImageIndex, descriptionMore, addedToCart } = this.state;
    const variantPickerPlaceholderText = t('choose_variant');

    console.log('%c%s', 'color: #21db1b', images.length);
    const freeReturnText = t('free_return');

    const getButtonText = () => {
      const addToCartText = t('add_to_cart');
      const addedToCartText = t('added_to_cart');
      const unavailableText = t('unavailable');
      if (product.isInStock && addedToCart && !this.props.hasErrorAddingToCart) {
        return addedToCartText;
      } else if (!product.isInStock) {
        return unavailableText;
      } else if (this.props.hasErrorAddingToCart) {
        return 'Failed added to cart'; // TODO : translations of Failed added to cart
      } else {
        return addToCartText;
      }
    };

    const variantsDropdownOptions =
      product && product.variants.length > 1
        ? product.variants
            .map((variant) => ({
              label: variant.attributesValue.replace('|', ' x '),
              value: variant.id,
              available: variant.isInStock,
            }))
            .filter((a) => a.available)
            .sort((a, b) => a.label.localeCompare(b.label, undefined, { numeric: true }))
        : [];

    const isProductAddToCartClickable =
      product.isInStock &&
      !this.props.hasErrorAddingToCart &&
      (!product.variants || selectedVariantOption || (product.variants && product.variants.length < 2));
    const freeShippingText = t('free_shipping');
    const isFreeShipping = product.deliveryCost && product.deliveryCost.value === 0;
    const shippingCostLabel = isFreeShipping ? freeShippingText : t('delivery_cost') + ': ';

    return (
      <div className="te-product-container">
        <ReactResizeDetector handleWidth onResize={this.handleContainerWidthChange}>
          {({ width }) => (
            <div className={cx('te-product', this.getClassNameByWidth(width))}>
              <figure className="te-product-image">
                {images.length > 1 && (
                  <figure className="te-product-thumbnails">
                    <Slider
                      {...this.sliderSettings}
                      nextArrow={
                        <CarouselArrow
                          type={this.sliderSettings.vertical ? 'down' : 'next'}
                          slidesToShow={this.sliderSettings.slidesToShow}
                        />
                      }
                      prevArrow={
                        <CarouselArrow
                          type={this.sliderSettings.vertical ? 'up' : 'prev'}
                          slidesToShow={this.sliderSettings.slidesToShow}
                        />
                      }
                    >
                      {images.map((image, index) => {
                        return (
                          <div
                            className="te-product-thumb"
                            onClick={() => this.changeCurrentImageIndex(index)}
                            onMouseEnter={() => this.changeCurrentImageIndex(index)}
                            key={index}
                            style={{ boxSizing: 'border-box' }}
                          >
                            <CloudinaryImg fit="contain" cloudinaryId={image['id']} />
                          </div>
                        );
                      })}
                    </Slider>
                  </figure>
                )}

                {images[currentImageIndex] && (
                  <InnerImageZoom
                    src={images[currentImageIndex]['450x']}
                    zoomSrc={images[currentImageIndex]['960x']}
                    fullscreenOnMobile={false}
                  ></InnerImageZoom>
                )}
              </figure>

              <div className="te-product-details">
                <ProductBasicInfo product={product} selectedVariant={selectedVariant} />
                {product.variants && product.variants.length > 1 && (
                  <Dropdown
                    onChange={this.handleVariantChange}
                    options={variantsDropdownOptions}
                    placeholder={variantPickerPlaceholderText}
                    validate={variantSelectorValidationError}
                  />
                )}

                <PrimaryButton
                  className={cx({
                    'added-to-cart': addedToCart,
                  })}
                  onClick={() => isProductAddToCartClickable && this.addToCartClicked()}
                  disabled={!isProductAddToCartClickable}
                >
                  {getButtonText()}
                </PrimaryButton>

                <div className="te-product-info-container">
                  <ul className="te-product-usp">
                    <li className="te-product-usp-item">
                      <div className="te-product-info">
                        <div className="te-product-info-label">{t('availability') + ': '}</div>
                        <div className="te-product-info-value">
                          {product.isInStock ? t('in_stock') : t('unavailable')}
                        </div>
                      </div>
                    </li>
                    <li className="te-product-usp-item">
                      <span className="te-product-info">
                        <span className="te-product-info-label">{t('delivery_time') + ': '}</span>
                        <span className="te-product-info-value">{product.deliveryTime}</span>
                      </span>
                    </li>
                    <li className="te-product-usp-item">
                      <span className="te-product-info">
                        <span className="te-product-info-label">{shippingCostLabel}</span>
                        {!isFreeShipping && (
                          <span className="te-product-info-value">{product.deliveryCost.formatted}</span>
                        )}
                      </span>
                    </li>
                    {product.freeReturn && (
                      <li className="te-product-usp-item">
                        <span className="te-product-info">
                          <span className="te-product-info-label">{freeReturnText}</span>
                        </span>
                      </li>
                    )}
                  </ul>
                </div>
                <div
                  className={cx('te-product-description', { more: descriptionMore })}
                  onClick={this.toggleDescriptionMore}
                  style={{ cursor: this.props.descriptionExpanded ? 'auto' : 'pointer' }}
                >
                  <span className="te-product-description-title">
                    {t('description')}
                    {this.props.descriptionExpanded ? null : <SmallArrowDown />}
                  </span>
                  <div
                    className={cx('te-product-description-content', { more: descriptionMore })}
                    dangerouslySetInnerHTML={{ __html: marked(product.description) }}
                  />
                  <span className="te-product-description-more">
                    {this.state.descriptionMore ? 'Show Less' : 'Show More'}
                  </span>
                </div>
              </div>
            </div>
          )}
        </ReactResizeDetector>
      </div>
    );
  }
}

export const ProductRenderer: ComponentType<ProductRendererProps> = connectedElement(ProductRendererNotConnected);
